//best practice: put new components in a new file -> add it backto main file : app.js
//Name convention: OnewordSubword

//component is React: JUST JAVASCRIPT FUNCTION

//Create a component -> export it back to main js file

//import css
import "./ExpenseItem.css";

//props function: hold the value that we get for custom element in app.js

function ExpenseItem(props) {
  //const expenseDate = new Date(2021, 2, 28); //create a date
  //const expenseTitle = "Car insurance";
  const expenseAmount = 294.67;
  const month = props.date.toLocaleString("en-US", { month: "long" });
  const day = props.date.toLocaleString("en-US", { day: "2-digit" });
  const year = props.date.getFullYear();

  //ONLY 1 ROOT element is allowed. ie: only 1 parent div element
  //className = class
  //this div is stil JS code, not HTML
  //{} : dynamic place holder, where we can dynamically inject our inputs
  // toLocaleString() output date. JS method
  return (
    <div className="expense-item">
      <div>
        <div>{month}</div>
        <div>{year}</div>
        <div>{day}</div>
      </div>
      {/* IOS string is a built in method */}

      <div className="expense-item__description">
        {/* props.title -> has to be the name you pick for attribute in app.js. props stands for properties. it is being used for passing data from one component to another
        in this case props = expenses.title*/}
        <h2>{props.title}</h2>
        <div className="expense-item__price">${expenseAmount}</div>
      </div>
    </div>
  );
}

export default ExpenseItem;
